
.drop .active {
  border-bottom: none;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.drop-group{
  padding-left:5px;
  padding-right:5px;
  font-weight: bold;
  font-size: 13px;
}
.drop-search {
  padding: 10px;
  border-left: 1px solid #cbd5dd;
  border-right: 1px solid #cbd5dd;
}
.drop-list-container{
  background: #ffffff;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  z-index: 2000;
  position: absolute;
  width: 100%;
}
.drop-list{
  background: #ffffff;
  list-style-type:none;
  padding:5px;
  border-bottom: 1px solid #cbd5dd;
  border-left: 1px solid #cbd5dd;
  border-right: 1px solid #cbd5dd;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  width: 100%;
  max-height: 300px;
  overflow: auto;
  margin-bottom: 0px;
}
.drop-item {
  padding: 3px 15px;
  font-size: 10px;
}
.drop-item:hover{
  background: #848484;
  color: #fff;
  cursor: pointer;
}

